@import url(https://fonts.googleapis.com/css?family=Pacifico|Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #38b3be;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100%;
  min-height: 100vh;
  background-color: #1f1f1f;
}

h1 {
  font-size: 7em;
  margin-bottom: 25px;
  font-family: 'Pacifico', cursive;
}

h2 {
  font-size: 2.5em;
  margin-top: 50px;
  text-align: center;
  color: #55e888;
  font-family: 'Pacifico', cursive;
}

.listItem {
  color: #1f1f1f;
  width: 100vw;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5em;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#textField {
  width: 500px;
  height: 50px;
  font-size: 2em;
  text-align: center;
  background-color: #1f1f1f;
  border: 3px solid #5f5f5f;
  border-radius: 25px;
  outline: none;
  margin-bottom: 25px;
  font-family: 'Roboto', sans-serif;
  padding: 2px;
}

#isBlue {
  width: 400px;
  height: 50px;
  font-size: 2em;
  background-color: #38b3be;
  border-radius: 25px;
  color: #1f1f1f;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
}

#isGreen {
  width: 400px;
  height: 50px;
  font-size: 2em;
  background-color: #55e888;
  border-radius: 25px;
  color: #1f1f1f;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
}

#isGreen:hover {
  background-color: #1f1f1f;
  border: 3px solid #55e888;
  color: #55e888;
}

